<template>
  <div class="students">
    <head-top :title="className">
    </head-top>

    <van-cell-group>
      <van-cell class="cell_main" v-for="(item, index) in personList" :key="index">
        <template slot="icon">
          <img :src="item.imgSrc" alt=""/>
        </template>
        <template slot="title">
          <span class="line_title">{{item.name}}</span>
        </template>
        <template slot="label">
          <span class="line_title">{{item.sno}}</span>
        </template>
        <template slot="right-icon" v-if="canOperate">
          <van-icon slot="right-icon" name="delete" @click.stop.prevent="toDelete(item)"></van-icon>
        </template>
      </van-cell>
    </van-cell-group>

  </div>
</template>

<script>
import { Dialog } from 'vant';
import defaultImg from "../../../../assets/boy_img.png";

export default {
  name: "students",
  data () {
    return {
      classID: this.$store.getters.classID || "",
      className: this.$store.getters.class.className || "默认班级",
      personList: [],
      show: false,
      selectItem: {},
      canOperate: this.$route.path.indexOf("course") === -1
    };
  },
  mounted () {
    this.getStudentList();
  },
  methods: {
    getStudentList () {
      this.personList = [];
      this.$mainHttp.student.dataList({
        classID: this.classID
      }).then(res => {
        if (res.Result) {
          res.Datas.forEach(item => {
            this.personList.push({
              imgSrc: defaultImg,
              name: item.Key["StudentName"],
              sno: item.Key["StudentXH"],
              userID: item.Key["UserID"],
              gender: item.Key["StudentGender"],
              id: item.Key["StudentID"]
            });
          })
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
        }
      }).catch(() => {
      });
    },
    toDelete (item) {
      Dialog.confirm({
        title: '警告',
        message: '确定将该学生移出班级？'
      }).then(() => {
        this.deleteStudent(item);
      }).catch(() => {
      });
    },
    deleteStudent (item) {
      this.$mainHttp.class.studentDelete({
        ids: item.id,
        classID: this.classID
      }).then(res => {
        if (res.Result) {
          this.$toast("删除成功！");
          this.getStudentList();
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("删除失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.students {
  min-height: 100%;
  background: #fff;
  margin-bottom: 50px;

  .cell_main {
    /deep/ .van-icon-delete {
      font-size: @font-title;
      color: #F56C6C;
    }
  }
}
</style>
